body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.orderItem {
  display: -webkit-flex;
  display: flex;
  padding: 11px 10px 11px 15px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.orderItem__picContainer {
  padding-right: 10px;
  display: inline-block;
}

.orderItem__pic {
  width: 90px;
  height: 90px;
  border: #777 solid 1px;
}

.orderItem__content {
  display: inline-block;
  width: calc(100% - 100px);
}

.orderItem__product {
  max-width: 237px;
  font-size: 17px;
  font-weight: 700;
  color: #111;
  padding-top: 6px;
  padding-right: 18px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.orderItem__shop {
  text-align: left;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 13px;
  color: #777;
  padding-top: 9px;
  padding-bottom: 13px;
}

.orderItem__detail {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 22px;
  line-height: 22px;
  padding-right: 4px;
}

.orderItem__price {
  font-size: 13px;
  color: #777;
  vertical-align: 1px;
  margin-left: 10px;
}

.orderItem__price::before {
  content: "\A5";
  margin-right: 1px;
}

.orderItem__btn {
  width: 80px;
  height: 22px;
  text-align: center;
  color: #fff;
  border: 0;
  font-size: 14px;
}

.orderItem__btn--red {
  background-color: #e9203d;
}

.orderItem__btn--grey {
  background-color: #999;
}

.orderItem__commentContainer {
  width: 97%;
  margin: 0 auto;
}
.orderItem__comment{
  width: 100%;
  height: 100px;
}
.orderItem_star--light{
  color: #fab700;
}
.header{
  background-color: #e9203d;
  color: #fff;
  padding: 15px 15px;
  text-align: center;
  font-size: 16px;
  line-height: 1;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

